<template>
  <div class="wrapper_reports">
    <div class="container">
      <div class="header">
        <h2>{{ projects[selected_project].name }}</h2>
        <label>Отчеты</label>
      </div>
      <div class="content">
        <div class="new_report">
          <button @click="create_new_report">Создать</button>
        </div>
        <div class="reports">
          <div
            class="report"
            v-for="report in projects[selected_project].reports"
            :key="report.id"
          >
            <div class="left">
              <label>{{ report.name }}</label
              ><small>{{ reverse_date(report.date) }}</small>
            </div>
            <div class="right"><button>1</button></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "RepotsView",
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["projects"]),
    ...mapGetters(["selected_project"]),
  },
  methods: {
    create_new_report() {
      this.$router.push("/projects/reports/newreport");
    },
    reverse_date(date) {
      return date.split("-").reverse().join("-");
    },
  },
};
</script>

