<template>
  <div class="wrapper_edit_user">
    <div class="container">
      <div class="header">
        <label>Управление доступами к проектам</label>
      </div>
      <div class="content">
        <div class="row" v-for="(project, idx) in projects" :key="project.id">
          <div class="row_head">
            <label class="name">{{ project.name }}</label>
            <label class="move" @click="move_to_project(idx)">
              Перейти к проекту
            </label>
          </div>
          <div class="row_content">
            <SelectorVue
              :options_props="options_role"
              :project_idx="idx"
              @select="option_select_role"
              :selected_option="users[user_idx].projects[idx].role"
            />
            <MultiSelector
              :options_props="options_accesses"
              :project_idx="idx"
              @select="option_select_accesses"
              :selected_options="users[user_idx].projects[idx].accesses"
            />
          </div>
        </div>
      </div>
      <div class="footer">
        <button
          @click="
            close_show_edit_user();
            return_old_value();
          "
        >
          Отмена
        </button>
        <button @click="close_show_edit_user()">Готово</button>
      </div>
    </div>
  </div>
</template>

<script>
import SelectorVue from "@/components/AppComponents/SelectorVue.vue";
import MultiSelector from "@/components/AppComponents/MultiSelector.vue";
import { mapGetters } from "vuex";
export default {
  name: "EditUser",
  components: {
    SelectorVue,
    MultiSelector,
  },
  props: {
    user_idx: Number,
  },
  data() {
    return {
      old_users: [],
    };
  },
  computed: {
    ...mapGetters(["users"]),
    ...mapGetters(["projects"]),
    ...mapGetters(["options_role"]),
    ...mapGetters(["options_accesses"]),
    ...mapGetters(["show_edit_user"]),
  },
  mounted() {
    if (this.show_edit_user === true) {
      this.old_users = [];
      this.users.forEach((val) => this.old_users.push(val));
    }
  },
  watch: {
    old_users: {
      handler: function () {
        // console.log(this.old_users[0].projects[0].role);
      },
      deep: true,
    },
  },
  methods: {
    option_select_role(option, idx) {
      const payload = {
        user_idx: this.user_idx,
        project_idx: idx,
        role: option,
      };
      this.$store.commit("update_role", payload);
    },
    option_select_accesses(options, idx) {
      const payload = {
        user_idx: this.user_idx,
        project_idx: idx,
        accesses: options,
      };
      this.$store.commit("update_accesses", payload);
    },
    close_show_edit_user() {
      this.$store.commit("close_show_edit_user");
    },
    return_old_value() {
      this.$store.commit("update_users", this.old_users);
    },
    move_to_project(idx) {
      this.$router.push("/projects/reports");
      this.$store.commit("select_project", idx);
      this.close_show_edit_user();
    },
  },
};
</script>

