<template>
  <div class="add_new" v-if="show_add">
    <label>Введите название</label>
    <input type="text" v-model="name_new_project" />
    <div class="btns">
      <button @click="close_modal()">Отмена</button>
      <button @click="add_new_project()">Добавить</button>
    </div>
  </div>
  <div class="wrapper_projects">
    <div class="container">
      <div class="header"><label>Проекты</label></div>
      <div class="content">
        <div class="new_item item" @click="show_add = true">Создать</div>
        <div
          class="item current_item"
          v-for="(project, idx) in projects"
          :key="project.id"
          @click="select_project(idx)"
        >
          <div class="footer">
            <label>{{ project.name }}</label>
            <button>1</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "ProjectsView",
  data() {
    return {
      name_new_project: "",
      show_add: false,
    };
  },
  computed: {
    ...mapGetters(["projects"]),
  },
  methods: {
    select_project(idx) {
      this.$store.commit("select_project", idx);
      this.$router.push("/projects/reports");
    },
    add_new_project() {
      if (this.name_new_project != "") {
        const new_project = {
          id: this.projects.lenght + 1,
          name: this.name_new_project,
          reports: [],
        };
        this.$store.commit("add_new_project", new_project);
        this.close_modal();
      }
    },
    close_modal() {
      this.show_add = false;
      this.name_new_project = "";
    },
  },
};
</script>

