<template>
  <div class="wrapper_new_report">
    <div class="container">
      <div class="header"><h2>Новый отчет</h2></div>
      <div class="content">
        <div class="top">
          <input type="text" v-model="new_report.name" class="input" />
        </div>
        <div class="main">
          <div class="header"><h3>Системы</h3></div>
          <div
            class="title"
            v-for="(stm, idx) in new_report.systems"
            :key="idx"
          >
            <SelectorVue
              :options_props="options_system"
              @select="option_select_system"
              :project_idx="idx"
              :selected_option="stm.system"
            />
            <SelectorVue
              :options_props="options_essence"
              @select="option_select_essence"
              :project_idx="idx"
              :selected_option="stm.essence"
            />
            <SelectorVue
              :options_props="options_source"
              @select="option_select_source"
              :project_idx="idx"
              :selected_option="stm.source"
            />
            <SelectorVue
              :options_props="options_events"
              @select="option_select_events"
              :project_idx="idx"
              :selected_option="stm.events"
            />
            <input type="text" class="input" />
          </div>
          <div class="bottom">
            <button class="btn" @click="add_system()">Добавить систему</button>
            <button class="btn">Добавить существующую систему</button>
          </div>
        </div>
      </div>
      <div class="footer">
        <SelectorVue
          :options_props="options_format"
          @select="option_select_format"
          :selected_option="new_report.format"
        />
        <div class="btns">
          <button class="btn2 btn">Вывести на главную</button>
          <button @click="create_report()" class="btn3 btn">
            Создать отчет
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SelectorVue from "@/components/AppComponents/SelectorVue.vue";
import { mapGetters } from "vuex";
export default {
  name: "NewRepotView",
  components: {
    SelectorVue,
  },
  data() {
    return {
      new_report: {
        id: 1,
        name: "",
        date: null,
        systems: [
          {
            system: { name: "amoCRM", value: 1 },
            essence: { name: "Контакты", value: 1 },
            source: { name: "Событие", value: 1 },
            events: { name: "Событие 1", value: 1 },
          },
        ],
        format: { name: "Текстовый", value: 1 },
      },
    };
  },
  created() {
    console.log(this.new_report.systems[0].system);
  },
  computed: {
    ...mapGetters(["selected_project"]),
    ...mapGetters(["projects"]),
    ...mapGetters(["options_system"]),
    ...mapGetters(["options_essence"]),
    ...mapGetters(["options_source"]),
    ...mapGetters(["options_events"]),
    ...mapGetters(["options_format"]),
  },
  methods: {
    option_select_system(option, idx) {
      Object.assign(this.new_report.systems[idx].system, option);
    },
    option_select_essence(option, idx) {
      Object.assign(this.new_report.systems[idx].essence, option);
    },
    option_select_source(option, idx) {
      Object.assign(this.new_report.systems[idx].source, option);
    },
    option_select_events(option, idx) {
      Object.assign(this.new_report.systems[idx].events, option);
    },
    option_select_format(option) {
      Object.assign(this.new_report.format, option);
    },
    add_system() {
      this.new_report.systems.push({
        system: { name: "amoCRM", value: 1 },
        essence: { name: "Контакты", value: 1 },
        source: { name: "Событие", value: 1 },
        events: { name: "Событие 1", value: 1 },
      });
    },
    create_report() {
      const date = new Date();
      const dd = String(date.getDate()).padStart(2, "0");
      const mm = String(date.getMonth() + 1).padStart(2, "0");
      const yyyy = date.getFullYear();
      this.new_report.date = yyyy + "-" + mm + "-" + dd;

      if (this.new_report.name != "") {
        this.new_report.id =
          this.projects[this.selected_project].reports.length + 1;
        this.$store.commit("add_new_report", this.new_report);
        this.$router.push("/projects/reports");
      }
    },
  },
};
</script>

